/**
 * @author Jean-Sbastien Conan <jean-sebastien@taotesting.com>
 */
define('taoProctoring/helper/contentLoader',[
    'jquery',
    'lodash',
    'core/promise'
], function ($, _, Promise) {
    'use strict';

    /**
     * Checks if the inline data set is empty. Default behaviour.
     * @param {Object} data
     * @returns {Boolean}
     */
    function isDataEmpty(data) {
        return _.isEmpty(data);
    }

    /**
     * Gets the content for the current page.
     * Tries first to extract the data set from the container (HTML5 data attributes).
     * Then if not content was found, request the server through the provided url.
     *
     * @param {jQuery} $container - The container that may have data in its attributes
     * @param {String} [url] - The url to request if no data was found
     * @param {Function} [checkEmpty] - A callback that will check if the inline data is fulfilled
     * @returns {Promise} Will be resolved with the content data
     */
    function getContentData($container, url, checkEmpty) {
        var data = $container.data();

        checkEmpty = _.isFunction(checkEmpty) ? checkEmpty : isDataEmpty;

        if (checkEmpty(data) && url) {
            return requestData(url);
        } else {
            return Promise.resolve(data);
        }
    }

    /**
     * Requests data from the server.
     *
     * @param {String} url - The url to request te data
     * @returns {Promise} Will be resolved with the requested data
     */
    function requestData(url) {
        return new Promise(function(reject, resolve) {
            $.ajax({
                url: url,
                cache: false,
                dataType : 'json',
                type: 'GET'
            }).done(function(response) {
                if (response && response.success) {
                    resolve(response.data);
                } else {
                    reject(response);
                }
            }).fail(reject);
        });
    }

    return {
        getContentData: getContentData,
        requestData: requestData
    };
});

