define('taoProctoring/controller/TestCenter.bak/index',[
    'jquery',
    'lodash',
    'i18n',
    'util/url',
    'layout/loading-bar',
    'ui/listbox',
    'taoProctoring/controller/app',
    'taoProctoring/helper/contentLoader',
    'taoProctoring/helper/containerAccess',
    'taoProctoring/component/breadcrumbs',
    'taoProctoring/helper/textConverter',
    'tpl!taoProctoring/templates/testSiteAdmin/adminLink'
], function ($, _, __, urlHelper, loadingBar, listBox, appController, contentLoader, containerAccess, breadcrumbsFactory, textConverter, adminLinkTpl) {
    'use strict';

    /**
     * The CSS scope
     * @type {String}
     */
    var cssScope = 'testcenters-index';

    // the page is always loading data when starting
    loadingBar.start();

    /**
     * Controls the taoProctoring index page
     *
     * @type {Object}
     */
    var taoProctoringIndexCtlr = {
        /**
         * Entry point of the page
         */
        start: function start() {
            var $container = containerAccess.getContainer(cssScope);
            var serviceUrl = urlHelper.route('index', 'TestCenter', 'taoProctoring');
            var adminUrl = urlHelper.route('index', 'ProctorManager', 'taoProctoring');
            var list = listBox({
                title: __("My Test sites"),
                textEmpty: __("No test site available"),
                textNumber: __("Available"),
                textLoading: __("Loading"),
                renderTo: $container.find('.content'),
                replace: true
            });
            var labels, data;

            list.setLoading(true);
            Promise.all([
                textConverter().then(function (lbls) {
                    labels = lbls;
                }),
                contentLoader.getContentData($container, serviceUrl).then(function (dat) {
                    data = dat;
                })
            ]).then(function () {
                breadcrumbsFactory($container, data.breadcrumbs);

                _.forEach(data.list, function(box) {
                    box.cls = ((box.cls || '') + ' router').trim();
                });
                list.update(data.list);
                loadingBar.stop();

                // add the test center admin link when appropriate
                if (data.administrator) {
                    $container.find('.header').append(adminLinkTpl({
                        href: adminUrl,
                        manageProctorMenu: labels.get('Manage Proctors')
                    }));
                }
            }).catch(function (err) {
                appController.onError(err);
            });
        }
    };

    return taoProctoringIndexCtlr;
});

