define('taoProctoring/controller/TestCenter.bak/testCenter',[
    'jquery',
    'i18n',
    'util/url',
    'layout/loading-bar',
    'ui/listbox',
    'taoProctoring/controller/app',
    'taoProctoring/helper/contentLoader',
    'taoProctoring/helper/containerAccess',
    'taoProctoring/component/breadcrumbs'
], function ($, __, urlHelper, loadingBar, listBox, appController, contentLoader, containerAccess, breadcrumbsFactory) {
    'use strict';

    /**
     * The CSS scope
     * @type {String}
     */
    var cssScope = 'testcenters-testcenter';

    // the page is always loading data when starting
    loadingBar.start();

    /**
     * Controls the taoProctoring test site page
     *
     * @type {Object}
     */
    var taoProctoringTestSiteCtlr = {
        /**
         * Entry point of the page
         */
        start : function start() {
            var $container = containerAccess.getContainer(cssScope);
            var serviceUrl = urlHelper.route('testCenter', 'TestCenter', 'taoProctoring');
            var currentRoute = urlHelper.parse(window.location.href);
            var initUrl = urlHelper.build(serviceUrl, {testCenter: decodeURIComponent(currentRoute.query.testCenter)});
            var list = listBox({
                title: "",
                textEmpty: false,
                textNumber: false,
                textLoading: __("Loading"),
                renderTo: $container.find('.content'),
                replace: true,
                width: 12
            });

            list.setLoading(true);
            console.log('requiring', initUrl);
            contentLoader.getContentData($container, initUrl).then(function (data) {
                console.log('received', data);
                breadcrumbsFactory($container, data.breadcrumbs);
                list.setTitle(data.title);
                list.update(data.list);
                loadingBar.stop();
                console.log('finished', list);
            }).catch(function (err) {
                appController.onError(err);
            });
        }
    };

    return taoProctoringTestSiteCtlr;
});

