/**
 * @author Jean-Sbastien Conan <jean-sebastien@taotesting.com>
 */
define('taoProctoring/controller/app',[
    'jquery',
    'core/historyRouter',
    'util/url',
    'layout/loading-bar',
    'ui/feedback',
    'taoProctoring/helper/containerAccess'
], function ($, historyRouterFactory, urlUtil, loadingBar, feedback, containerAccess) {
    'use strict';

    /**
     * Shared router that will manage the page for each controller
     * @type {historyRouter}
     */
    var historyRouter;

    /**
     * The main controller that will manage the routes through the history
     * @typedef {appController}
     */
    var appController = {
        /**
         * App controller entry point: set up the router.
         */
        start: function start() {
            // all links that are tagged with the "router" class are dispatched using the history router
            $(document).on('click', '.router', function (e) {
                var $elt, href;

                // prevent the browser to actually change the page from this link
                e.preventDefault();

                // try to get the target of the link
                $elt = $(this);
                href = $elt.attr('href');
                if (!href) {
                    href = $('[href]:first-child', $elt).attr('href');
                }

                // use the history router to change the current view
                // the called controller will have in charge to get the data and update the view accordingly
                if (href) {
                    historyRouter.trigger('dispatch', href);
                }
            });

            // setup the history router to update the layout while changing view
            historyRouter = historyRouterFactory()
            //empty the content
                .on('dispatching', function (url) {
console.log('dispatching', url);
                    containerAccess.clearContainer();

                    // todo: update the controls
                    $('.content-wrap section').remove();
                })

                //mark the current navigation as active
                .on('dispatched', function (url) {
                    var dispatchedUrl = urlUtil.parse(url);
console.log('dispatched', url);
                    if (dispatchedUrl && dispatchedUrl.path) {
                        // todo: update the controls
                        $('.content-wrap > nav .router').removeClass('active').each(function () {
                            var $elt = $(this);
                            var parsed = urlUtil.parse($('a', $elt).attr('href'));
                            if (parsed && parsed.path === dispatchedUrl.path) {
                                $elt.addClass('active');
                            }
                        });
                    }
                });

            // dispatch the current route
            historyRouter.dispatch({
                url: window.location + ''
            }, true);
        },

        /**
         * Exposes the router so other controllers can dispatch a route
         *
         * @returns {router} the router
         */
        getRouter: function getRouter() {
            return historyRouter;
        },

        /**
         * Catches errors
         * @param {Object} err
         */
        onError: function onError(err) {
            var message = err && err.message || err;
            loadingBar.stop();

            feedback().error(message);

            //TODO to be replaced by the logger
            window.console.error(err);
        }
    };

    // auto start the controller as it must be ready as soon as possible to handle the history
    appController.start();

    return appController;
});

